:root {
  --pink: #dc1ff2;

  --yellow: #f5c32c;
  --orange: #fca61f;
  --black: #242d49;
  --gray: #788097;
  --blueCard: #ddf8fe;
  --purple: rgb(213, 163, 244);
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --pinkCard: rgba(252, 31, 245, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: var(--black);
}
.button {
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  background: linear-gradient(180deg, #a6418bd2 26.71%, #d015ba 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(160, 29, 121, 0.42);
}

.button:hover {
  background: white;
  cursor: pointer;
  border: 1px solid var(--pink);
  color: var(--pink);
}

@media screen and (max-width: 480px) {
  .App {
    padding: 0.5rem 1rem;
  }
}
